header {
    padding-top: 7rem;
    overflow: hidden;
}

.main__container{
    text-align: center;
    height: 100%;
    position: relative;
}

ul li {
    list-style-type: square;
    display: list-item;
    list-style-position: outside;
}

h4 {
    margin-bottom: 2.5rem;
}

h2 {
    margin-top: 2.5rem;
}
h3 {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}

/* TABLES */
table {
    width: 100%;
    border: 1px solid;
    border-collapse: collapse;
}

th, td {
    border-bottom: 1px solid #ddd;
}
#supported {
    background-color: #04AA6D;
}
#wip {
    background-color: #b9a21f;
}
#unsupported {
    background-color: #e75853;
}

.me {
    background-image: linear-gradient(45deg, var(--color-light), var(--color-white));
    height: 13rem;
    /*transform: skewY(-3deg);*/
}

.bluebar__top {
    margin-top: 2.5rem;
    background-image: linear-gradient(45deg, var(--osk-games-color-1), var(--osk-games-color-2));
    height: 1.5rem;
    /*transform: skewY(-3deg);*/
}

.me__content {
    max-width: 50em;
	margin: 0 auto;
    transform: translateY(20%);
}

/* TEXT */
.subtitle {
    font-style: italic;
    margin-top: 1rem;
    margin-bottom: 3rem;
}

.leftAligned {
    text-align: left;
    margin-top: 1rem;
    z-index: 100;
}

.images {
    position: absolute;
    image-resolution: 50%;
    height: fit-content;
}

.lista {
    margin-bottom: 6rem;
}

.overImage {
    position: absolute;
}

.horizontalLayout {
    margin-top: 2rem;
    overflow: hidden;
}

.horizontalLayoutElement {
    float: left;
    width: 33%;
}