.mainImage {
    margin-right: 5rem;
    width: 15%;
    border-radius: 50%;
    border:0.2rem solid rgb(255, 255, 255);
    border-top: 0rem;

    margin-top: 2em;

}

.mainName {
    font-size: 5rem;
}

.secondaryName {
    margin-top: 0.5rem;
    font-size: 3rem;
    color: rgb(205, 219, 230);
}

.horizontalLayout {
    display: flex;
    align-items: center;
    margin-top: 2rem;
    justify-content: center;
}

.button1 {
    background: rgb(196, 203, 221);
    color:rgb(7, 13, 29);
    border-color: rgb(7, 13, 29);
    border-width: 0rem;
    width: 8rem;
    margin-right: 1rem;
    margin-bottom: 0.1rem;
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
}
.button1:hover {
    background: rgb(150, 180, 255);
    color: rgb(1, 5, 1);
    width: 10rem;
}

.verticalLayout {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
    justify-content: center;
    flex-direction: column;
}

.verticalBlogLayout {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
}

.centeredText {
    text-align: center;
}

.whiteLine {
    height: 0.5em;
    background-color: white;
    margin-left: 3rem;
    margin-right: 3rem;
    width: 100%;
}

.projectImage {
    background-color: rgb(230, 242, 252);
    border: 0.5rem solid;
    border-image: linear-gradient(-45deg, rgb(41, 187, 255), rgb(0, 132, 255));
    border-image-slice: 1;
}

.verticalLayout-project {
    margin-top: 2rem;
    border-top: 0.2em solid rgb(199, 219, 250);
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: -2rem;
    top: 9.4rem;
}
